import React, { Component } from 'react';
import { connect } from 'react-redux';
import { toggleArray } from '../components/utils'

// actions
import JobsActions from '../redux/redux-sauce/jobs';
import IsubsActions from '../redux/redux-sauce/isubs';


function JobBlock (props) {
  const { job } = props
  return (
    <div className='card card-style my-2'>
      <div className="content text-left">
          <h4 className='font-16 font-500 float-left'>{job.title}</h4>
          <h4 className='font-18 float-right'>{job.budget}</h4>
          <div className="clearfix"></div>
          <p className='text-left float-left mb-0 font-12 font-500'>{job.zakazchik}</p>
          <p className='text-left float-right mb-0 font-11 font-400 font-italic'>{job.category}</p>
          <div className="clearfix"></div>
          <p className="text-left mb-0">
            {job.description}
          </p>
          <p className='text-left float-left mb-0'>{job.address}</p>
          {job.start_date ? 
              <p className='text-left float-right mb-0'>c {job.start_date}  {job.end_date}</p>
              :
              job.end_date ?
                <p className='text-left float-right mb-0'> {job.end_date}</p>
                :
                <p className='text-left float-right mb-0'>  </p>
            }
          <div className="clearfix"></div>
          <p className='text-left float-left font-10'>{job.created_ago}</p>
          {job.images.length > 0 && <p className='text-left float-right font-15'>{job.images.length} </p>}
      </div>
    </div>
  )
}


class JobDetail extends Component {
  constructor(props) {
    super(props);
    this.state = {
      needToRefresh: false,
    }
  }

  componentDidMount() {
    console.log(this.props.match)
    // this.props.getJob()
    // if (this.props.match)
    const { id } = this.props.match.params
    console.log(id)
    this.props.getJobDetail(id)
  }

  render() {
    const { jobDetail, detailFetching } = this.props.state.jobs

    // console.log(this.props.state)
    return (
        jobDetail 
            ? <JobBlock job={jobDetail} />
            : ' '
    );
  }
}

const mapStateToProps = (state) => ({
  notifications: state.notifications,
  state: state,
  filterCategories: state.isubs.filterCategories
})

const mapDispatchToProps = (dispatch) => ({
  getCategories: query => dispatch(JobsActions.getCategoriesRequest(query)),
  getJobDetail: query => dispatch(JobsActions.getJobDetailRequest(query)),
})

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(JobDetail);
